import React, { useState } from "react";
import Modal from "../../common/Modal";

const ProductList = ({
	products,
	onProductSelect,
	selectedProductId,
	isLoading,
	error,
	onRefresh,
	onEdit,
	onDelete,
}) => {
	const [editProduct, setEditProduct] = useState(null);
	const [showEditModal, setShowEditModal] = useState(false);
	const [showDeleteModal, setShowDeleteModal] = useState(false);
	const [deleteProductId, setDeleteProductId] = useState(null);
	const [formData, setFormData] = useState({
		name: "",
		price: 0,
		description: "",
	});
	const [formLoading, setFormLoading] = useState(false);
	const [formError, setFormError] = useState(null);
	const [notification, setNotification] = useState(null);

	const openEditModal = (product) => {
		setEditProduct(product);
		setFormData({
			name: product.name,
			price: product.price,
			description: product.description || "",
		});
		setFormError(null);
		setShowEditModal(true);
	};

	const closeEditModal = () => {
		setShowEditModal(false);
		setEditProduct(null);
		setFormError(null);
	};

	const openDeleteModal = (productId) => {
		setDeleteProductId(productId);
		setShowDeleteModal(true);
	};

	const closeDeleteModal = () => {
		setShowDeleteModal(false);
		setDeleteProductId(null);
	};

	const handleFormChange = (e) => {
		const { name, value } = e.target;
		setFormData((prev) => ({
			...prev,
			[name]: name === "price" ? parseFloat(value) || 0 : value,
		}));
	};

	const handleFormSubmit = async (e) => {
		e.preventDefault();
		setFormLoading(true);
		setFormError(null);
		try {
			await onEdit({ ...editProduct, ...formData });
			setNotification("Produk berhasil diperbarui");
			closeEditModal();
		} catch (err) {
			setFormError("Gagal memperbarui produk: " + err.message);
		} finally {
			setFormLoading(false);
		}
	};

	const handleDeleteConfirm = async () => {
		if (!deleteProductId) return;
		try {
			await onDelete(deleteProductId);
			setNotification("Produk berhasil dihapus");
		} catch (err) {
			setNotification("Gagal menghapus produk: " + err.message);
		} finally {
			closeDeleteModal();
		}
	};

	return (
		<div className="bg-white rounded-xl shadow-md p-6">
			<div className="flex justify-between items-center mb-6">
				<h2 className="text-2xl font-bold text-gray-800">Existing Products</h2>
				<div className="flex items-center space-x-3">
					{!isLoading && (
						<span className="bg-indigo-100 text-indigo-800 px-3 py-1 rounded-full text-sm">
							{products.length} items
						</span>
					)}
					<button
						onClick={onRefresh}
						disabled={isLoading}
						className="bg-indigo-600 hover:bg-indigo-700 text-white px-3 py-1 rounded text-sm disabled:opacity-50"
					>
						Refresh Produk
					</button>
				</div>
			</div>

			{notification && (
				<div className="bg-green-100 text-green-700 p-3 rounded mb-4">
					{notification}
					<button
						className="ml-4 font-bold"
						onClick={() => setNotification(null)}
					>
						X
					</button>
				</div>
			)}

			{isLoading ? (
				<div className="flex justify-center py-8">
					<div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-indigo-500"></div>
				</div>
			) : error ? (
				<div className="bg-red-50 text-red-600 p-4 rounded-lg">
					{error}
					<button onClick={onRefresh} className="ml-2 text-red-700 underline">
						Coba Lagi
					</button>
				</div>
			) : products.length === 0 ? (
				<div className="text-center py-8">
					<p className="text-gray-500 mb-2">Tidak ada produk tersedia</p>
					<button
						className="text-indigo-600 hover:text-indigo-800 text-sm font-medium"
						onClick={onRefresh}
					>
						Refresh Data
					</button>
				</div>
			) : (
				<div className="space-y-3 max-h-[500px] overflow-y-auto pr-2">
					{products.map((product) => (
						<div
							key={product.id}
							className={`border rounded-lg p-4 hover:shadow-md transition cursor-pointer ${
								selectedProductId === product.id
									? "ring-2 ring-indigo-500 bg-indigo-50"
									: ""
							}`}
							onClick={() => onProductSelect?.(product)}
						>
							<div className="flex justify-between items-start">
								<div>
									<h3 className="font-bold text-gray-800">{product.name}</h3>
									<p className="text-gray-600 text-sm line-clamp-2">
										{product.description || "Tidak ada deskripsi"}
									</p>
								</div>
								<div className="text-right">
									<span className="font-medium text-indigo-600 block">
										Rp{product.price.toLocaleString("id-ID")}
									</span>
									<span className="text-xs text-gray-400">
										ID: {product.id}
									</span>
									<div className="mt-2 space-x-2">
										<button
											onClick={(e) => {
												e.stopPropagation();
												openEditModal(product);
											}}
											className="bg-yellow-400 hover:bg-yellow-500 text-white px-2 py-1 rounded text-xs"
										>
											Edit
										</button>
										<button
											onClick={(e) => {
												e.stopPropagation();
												openDeleteModal(product.id);
											}}
											className="bg-red-500 hover:bg-red-600 text-white px-2 py-1 rounded text-xs"
										>
											Delete
										</button>
									</div>
								</div>
							</div>
						</div>
					))}
				</div>
			)}

			{/* Edit Modal */}
			<Modal
				isOpen={showEditModal}
				title="Edit Produk"
				onClose={closeEditModal}
			>
				<form onSubmit={handleFormSubmit} className="space-y-4">
					<div>
						<label className="block text-sm font-medium text-gray-700 mb-1">
							Nama Produk
						</label>
						<input
							type="text"
							name="name"
							value={formData.name}
							onChange={handleFormChange}
							className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
							placeholder="Nama produk"
							required
						/>
					</div>
					<div>
						<label className="block text-sm font-medium text-gray-700 mb-1">
							Harga
						</label>
						<div className="relative">
							<span className="absolute left-3 top-2">Rp</span>
							<input
								type="number"
								name="price"
								value={formData.price}
								onChange={handleFormChange}
								className="w-full pl-8 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
								placeholder="0"
								min="0"
								step="any"
								required
							/>
						</div>
					</div>
					<div>
						<label className="block text-sm font-medium text-gray-700 mb-1">
							Deskripsi
						</label>
						<textarea
							name="description"
							value={formData.description}
							onChange={handleFormChange}
							className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
							rows={3}
							placeholder="Deskripsi produk"
						/>
					</div>
					{formError && (
						<p className="text-red-600 text-sm font-medium">{formError}</p>
					)}
					<div className="flex justify-end space-x-2">
						<button
							type="button"
							onClick={closeEditModal}
							className="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400"
						>
							Batal
						</button>
						<button
							type="submit"
							disabled={formLoading}
							className={`px-4 py-2 rounded bg-blue-600 text-white hover:bg-blue-700 disabled:opacity-50`}
						>
							{formLoading ? "Menyimpan..." : "Simpan"}
						</button>
					</div>
				</form>
			</Modal>

			{/* Delete Confirmation Modal */}
			<Modal
				isOpen={showDeleteModal}
				title="Konfirmasi Hapus"
				onClose={closeDeleteModal}
			>
				<p>Apakah Anda yakin ingin menghapus produk ini?</p>
				<div className="flex justify-end space-x-2 mt-4">
					<button
						onClick={closeDeleteModal}
						className="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400"
					>
						Batal
					</button>
					<button
						onClick={handleDeleteConfirm}
						className="px-4 py-2 rounded bg-red-600 text-white hover:bg-red-700"
					>
						Hapus
					</button>
				</div>
			</Modal>
		</div>
	);
};

export default ProductList;
