import React from "react";
import { useAuth } from "../../../App";
const ProductForm = ({
	newProduct,
	handleProductChange,
	createProduct,
	isLoading,
}) => {
	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			await createProduct();
			// Success handled in createProduct via fetchData
		} catch (error) {
			// Add error state to show user feedback
			console.error("Product creation failed:", error);
		}
	};

	return (
		<div className="bg-white rounded-xl shadow-md p-6">
			<h2 className="text-2xl font-bold mb-6 text-gray-800">
				Buat Produk baru
			</h2>
			<form onSubmit={handleSubmit} className="space-y-4">
				<div>
					<label className="block text-sm font-medium text-gray-700 mb-1">
						Nama Produk
					</label>
					<input
						type="text"
						name="name"
						value={newProduct.name}
						onChange={handleProductChange}
						className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
						placeholder="Nama produk"
						required
					/>
				</div>
				<div>
					<label className="block text-sm font-medium text-gray-700 mb-1">
						Harga
					</label>
					<div className="relative">
						<span className="absolute left-3 top-2">Rp</span>
						<input
							type="number"
							name="price"
							value={newProduct.price}
							onChange={handleProductChange}
							className="w-full pl-8 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
							placeholder="0"
							min="0"
							step=""
							required
						/>
					</div>
				</div>
				<div>
					<label className="block text-sm font-medium text-gray-700 mb-1">
						Deskripsi
					</label>
					<textarea
						name="description"
						value={newProduct.description}
						onChange={handleProductChange}
						className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
						rows={3}
						placeholder="Deskripsi produk"
					/>
				</div>
				<button
					type="submit"
					disabled={isLoading}
					className={`w-full bg-green-600 text-white py-3 rounded-lg font-medium hover:bg-green-700 transition ${
						isLoading ? "opacity-50 cursor-not-allowed" : ""
					}`}
				>
					{isLoading ? "Membuat..." : "Buat Produk"}
				</button>
			</form>
		</div>
	);
};

export default ProductForm;
