import React, { useState, useEffect } from "react";
import Modal from "../../common/Modal";

const PackageList = ({
	packages = [],
	isLoading,
	error,
	onEdit,
	onDelete,
	onRefresh,
}) => {
	const [itemsPerPage, setItemsPerPage] = useState(5);
	const [currentPage, setCurrentPage] = useState(1);
	const [searchTerm, setSearchTerm] = useState("");

	const [editPackage, setEditPackage] = useState(null);
	const [showEditModal, setShowEditModal] = useState(false);
	const [showDeleteModal, setShowDeleteModal] = useState(false);
	const [deletePackageId, setDeletePackageId] = useState(null);
	const [formData, setFormData] = useState({
		name: "",
		description: "",
		payment_method: "daily",
		payment_amount: 0,
		productIds: [],
	});
	const [formLoading, setFormLoading] = useState(false);
	const [formError, setFormError] = useState(null);
	const [notification, setNotification] = useState(null);

	const filteredPackages = packages.filter(
		(pkg) =>
			pkg.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
			pkg.description.toLowerCase().includes(searchTerm.toLowerCase())
	);
	const totalPages = Math.ceil(filteredPackages.length / itemsPerPage);
	const indexOfLastItem = currentPage * itemsPerPage;
	const indexOfFirstItem = indexOfLastItem - itemsPerPage;
	const currentPackages = filteredPackages.slice(
		indexOfFirstItem,
		indexOfLastItem
	);

	useEffect(() => {
		setCurrentPage(1);
	}, [searchTerm, itemsPerPage]);

	const openEditModal = (pkg) => {
		setEditPackage(pkg);
		setFormData({
			name: pkg.name || "",
			description: pkg.description || "",
			payment_method: pkg.payment_method?.toLowerCase() || "daily",
			payment_amount: pkg.payment_amount || 0,
			productIds: pkg.products ? pkg.products.map((p) => p.id) : [],
		});
		setFormError(null);
		setShowEditModal(true);
	};

	const closeEditModal = () => {
		setShowEditModal(false);
		setEditPackage(null);
		setFormError(null);
	};

	const openDeleteModal = (packageId) => {
		setDeletePackageId(packageId);
		setShowDeleteModal(true);
	};

	const closeDeleteModal = () => {
		setShowDeleteModal(false);
		setDeletePackageId(null);
	};

	const handleFormChange = (e) => {
		const { name, value } = e.target;
		setFormData((prev) => ({
			...prev,
			[name]: name === "payment_amount" ? parseFloat(value) || 0 : value,
		}));
	};

	const handleFormSubmit = async (e) => {
		e.preventDefault();
		setFormLoading(true);
		setFormError(null);
		try {
			await onEdit({ ...editPackage, ...formData });
			setNotification("Paket berhasil diperbarui");
			closeEditModal();
		} catch (err) {
			setFormError("Gagal memperbarui paket: " + err.message);
		} finally {
			setFormLoading(false);
		}
	};

	const handleDeleteConfirm = async () => {
		if (!deletePackageId) return;
		try {
			await onDelete(deletePackageId);
			setNotification("Paket berhasil dihapus");
		} catch (err) {
			setNotification("Gagal menghapus paket: " + err.message);
		} finally {
			closeDeleteModal();
		}
	};

	const nextPage = () => {
		if (currentPage < totalPages) {
			setCurrentPage(currentPage + 1);
		}
	};

	const prevPage = () => {
		if (currentPage > 1) {
			setCurrentPage(currentPage - 1);
		}
	};

	const goToPage = (page) => {
		setCurrentPage(page);
	};

	return (
		<div className="bg-white rounded-xl shadow-md p-6">
			<div className="flex justify-between items-center mb-6">
				<h2 className="text-2xl font-bold text-gray-800">Existing Packages</h2>
				<div className="flex flex-col sm:flex-row gap-3">
					{/* Items per page dropdown */}
					<div className="relative">
						<select
							value={itemsPerPage}
							onChange={(e) => setItemsPerPage(Number(e.target.value))}
							className="appearance-none pl-3 pr-8 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
						>
							{[5, 10, 25, 50, 100].map((num) => (
								<option key={num} value={num}>
									Show {num}
								</option>
							))}
						</select>
						<div className="absolute right-3 top-2.5 text-gray-400 pointer-events-none">
							<svg
								xmlns="http://www.w3.org/2000/svg"
								className="h-5 w-5"
								viewBox="0 0 20 20"
								fill="currentColor"
							>
								<path
									fillRule="evenodd"
									d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
									clipRule="evenodd"
								/>
							</svg>
						</div>
					</div>

					{/* Refresh button */}
					<button
						onClick={onRefresh}
						disabled={isLoading}
						className="bg-indigo-600 hover:bg-indigo-700 text-white px-3 py-2 rounded text-sm disabled:opacity-50 flex items-center gap-1"
					>
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-5 w-5"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor"
						>
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={2}
								d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
							/>
						</svg>
						Refresh
					</button>
				</div>
				{/* Search Input */}
				<div className="relative">
					<input
						type="text"
						placeholder="Search packages..."
						className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
						value={searchTerm}
						onChange={(e) => setSearchTerm(e.target.value)}
					/>
					<div className="absolute left-3 top-2.5 text-gray-400">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-5 w-5"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor"
						>
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={2}
								d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
							/>
						</svg>
					</div>
				</div>
			</div>

			{notification && (
				<div className="bg-green-100 text-green-700 p-3 rounded mb-4">
					{notification}
					<button
						className="ml-4 font-bold"
						onClick={() => setNotification(null)}
					>
						X
					</button>
				</div>
			)}

			{isLoading ? (
				<div className="flex justify-center py-8">
					<div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-indigo-500"></div>
				</div>
			) : error ? (
				<div className="bg-red-50 text-red-600 p-4 rounded-lg">
					{error}
					<button onClick={onRefresh} className="ml-2 text-red-700 underline">
						Coba Lagi
					</button>
				</div>
			) : packages.length === 0 ? (
				<div className="text-center py-8">
					<p className="text-gray-500 mb-2">No packages available</p>
					<button
						className="text-indigo-600 hover:text-indigo-800 text-sm font-medium"
						onClick={onRefresh}
					>
						Refresh Data
					</button>
				</div>
			) : (
				<div className="space-y-6">
					{currentPackages.map((pkg) => {
						if (!pkg) return null;

						const totalAmount =
							pkg.products?.reduce((sum, product) => {
								return sum + (product?.price || 0);
							}, 0) || 0;

						return (
							<div
								key={pkg.id}
								className="rounded-2xl border bg-white p-5 shadow-sm hover:shadow-md transition-all duration-200"
							>
								<div className="flex justify-between items-start gap-4">
									<div className="space-y-1">
										<h3 className="text-xl font-semibold text-gray-900">
											{pkg.name || "Unnamed Package"}
										</h3>
										{pkg.description && (
											<p className="text-gray-500 text-sm">{pkg.description}</p>
										)}
									</div>
									<span className="inline-block rounded-full bg-indigo-100 px-3 py-1 text-xs font-medium text-indigo-800 capitalize">
										{pkg.payment_method?.toLowerCase() || "unknown"}
									</span>
								</div>

								<div className="mt-4 space-y-2">
									<p className="text-sm text-gray-600">
										<span className="font-medium text-gray-800">Payment:</span>{" "}
										Rp{(pkg.payment_amount || 0).toLocaleString()} /{" "}
										{pkg.payment_method?.toLowerCase()}
									</p>

									{pkg.products?.length > 0 ? (
										<div>
											<h4 className="text-sm font-semibold text-gray-800">
												Products:
											</h4>
											<ul className="mt-1 divide-y text-sm text-gray-700">
												{pkg.products.map((product) => (
													<li
														key={product.id}
														className="flex justify-between py-1.5"
													>
														<span>{product.name || "Unnamed Product"}</span>
														<span>
															Rp{(product.price || 0).toLocaleString()}
														</span>
													</li>
												))}
											</ul>
											<div className="mt-2 pt-2 border-t text-right">
												<p className="font-semibold text-gray-900">
													Total: Rp{totalAmount.toLocaleString()}
												</p>
											</div>
										</div>
									) : (
										<p className="text-sm text-gray-400 mt-2">
											No products in this package
										</p>
									)}
								</div>

								<div className="mt-5 flex justify-end gap-2">
									<button
										onClick={() => openEditModal(pkg)}
										className="inline-flex items-center gap-2 rounded-md bg-yellow-400 hover:bg-yellow-500 text-white px-4 py-1.5 text-sm font-medium shadow-sm transition"
									>
										✏️ Edit
									</button>
									<button
										onClick={() => openDeleteModal(pkg.id)}
										className="inline-flex items-center gap-2 rounded-md bg-red-500 hover:bg-red-600 text-white px-4 py-1.5 text-sm font-medium shadow-sm transition"
									>
										🗑️ Delete
									</button>
								</div>
							</div>
						);
					})}
					{/* Pagination Controls - TEMPATKAN DI SINI */}
					{filteredPackages.length > itemsPerPage && (
						<div className="flex flex-col sm:flex-row items-center justify-between mt-6 gap-4">
							{/* Info jumlah item */}
							<div className="text-sm text-gray-600">
								Showing {indexOfFirstItem + 1}-
								{Math.min(indexOfLastItem, filteredPackages.length)} of{" "}
								{filteredPackages.length} packages
							</div>

							{/* Navigasi halaman */}
							<div className="flex items-center gap-2">
								{/* Tombol Previous */}
								<button
									onClick={prevPage}
									disabled={currentPage === 1}
									className="px-3 py-1 border rounded disabled:opacity-50 flex items-center gap-1"
								>
									<svg
										xmlns="http://www.w3.org/2000/svg"
										className="h-4 w-4"
										fill="none"
										viewBox="0 0 24 24"
										stroke="currentColor"
									>
										<path
											strokeLinecap="round"
											strokeLinejoin="round"
											strokeWidth={2}
											d="M15 19l-7-7 7-7"
										/>
									</svg>
									Previous
								</button>

								{/* Daftar nomor halaman */}
								<div className="flex gap-1">
									{Array.from({ length: Math.min(5, totalPages) }).map(
										(_, i) => {
											let pageNum;
											if (totalPages <= 5) {
												pageNum = i + 1;
											} else if (currentPage <= 3) {
												pageNum = i + 1;
											} else if (currentPage >= totalPages - 2) {
												pageNum = totalPages - 4 + i;
											} else {
												pageNum = currentPage - 2 + i;
											}

											return (
												<button
													key={pageNum}
													onClick={() => goToPage(pageNum)}
													className={`w-8 h-8 rounded flex items-center justify-center ${
														currentPage === pageNum
															? "bg-indigo-600 text-white"
															: "border hover:bg-gray-100"
													}`}
												>
													{pageNum}
												</button>
											);
										}
									)}
								</div>

								{/* Tombol Next */}
								<button
									onClick={nextPage}
									disabled={currentPage === totalPages}
									className="px-3 py-1 border rounded disabled:opacity-50 flex items-center gap-1"
								>
									Next
									<svg
										xmlns="http://www.w3.org/2000/svg"
										className="h-4 w-4"
										fill="none"
										viewBox="0 0 24 24"
										stroke="currentColor"
									>
										<path
											strokeLinecap="round"
											strokeLinejoin="round"
											strokeWidth={2}
											d="M9 5l7 7-7 7"
										/>
									</svg>
								</button>
							</div>
						</div>
					)}
				</div>
			)}

			{/* Edit Modal */}
			<Modal isOpen={showEditModal} title="Edit Paket" onClose={closeEditModal}>
				<form onSubmit={handleFormSubmit} className="space-y-5">
					{/* Nama Paket */}
					<div>
						<label className="block text-sm font-semibold text-gray-700 mb-1">
							Nama Paket
						</label>
						<input
							type="text"
							name="name"
							value={formData.name}
							onChange={handleFormChange}
							className="w-full rounded-xl border border-gray-300 px-4 py-2 text-sm shadow-sm focus:border-indigo-500 focus:ring-2 focus:ring-indigo-400 transition"
							placeholder="Contoh: Paket Premium"
							required
						/>
					</div>

					{/* Deskripsi */}
					<div>
						<label className="block text-sm font-semibold text-gray-700 mb-1">
							Deskripsi
						</label>
						<textarea
							name="description"
							value={formData.description}
							onChange={handleFormChange}
							className="w-full rounded-xl border border-gray-300 px-4 py-2 text-sm shadow-sm focus:border-indigo-500 focus:ring-2 focus:ring-indigo-400 transition"
							rows={3}
							placeholder="Contoh: Paket dengan layanan lengkap selama 1 bulan"
						/>
					</div>

					{/* Metode Pembayaran */}
					<div>
						<label className="block text-sm font-semibold text-gray-700 mb-1">
							Metode Pembayaran
						</label>
						<select
							name="payment_method"
							value={formData.payment_method}
							onChange={handleFormChange}
							className="w-full rounded-xl border border-gray-300 px-4 py-2 text-sm shadow-sm focus:border-indigo-500 focus:ring-2 focus:ring-indigo-400 transition"
							required
						>
							<option value="">Pilih metode</option>
							<option value="daily">Harian</option>
							<option value="weekly">Mingguan</option>
							<option value="monthly">Bulanan</option>
						</select>
					</div>

					{/* Jumlah Pembayaran */}
					<div>
						<label className="block text-sm font-semibold text-gray-700 mb-1">
							Jumlah Pembayaran
						</label>
						<input
							type="number"
							name="payment_amount"
							value={formData.payment_amount}
							onChange={handleFormChange}
							className="w-full rounded-xl border border-gray-300 px-4 py-2 text-sm shadow-sm focus:border-indigo-500 focus:ring-2 focus:ring-indigo-400 transition"
							min="0"
							step="any"
							required
						/>
					</div>

					{/* Error */}
					{formError && (
						<p className="text-sm text-red-600 font-medium">{formError}</p>
					)}

					{/* Action Buttons */}
					<div className="flex justify-end gap-2 pt-2">
						<button
							type="button"
							onClick={closeEditModal}
							className="rounded-lg bg-gray-200 text-gray-700 hover:bg-gray-300 px-4 py-2 text-sm transition"
						>
							Batal
						</button>
						<button
							type="submit"
							disabled={formLoading}
							className="rounded-lg bg-indigo-600 text-white hover:bg-indigo-700 px-4 py-2 text-sm font-medium transition disabled:opacity-50"
						>
							{formLoading ? "Menyimpan..." : "Simpan"}
						</button>
					</div>
				</form>
			</Modal>

			{/* Delete Confirmation Modal */}
			<Modal
				isOpen={showDeleteModal}
				title="Konfirmasi Hapus Paket"
				onClose={closeDeleteModal}
			>
				<p className="text-gray-700 text-sm">
					Yakin ingin menghapus paket ini? Tindakan ini tidak bisa dibatalkan.
				</p>
				<div className="flex justify-end gap-2 mt-6">
					<button
						onClick={closeDeleteModal}
						className="rounded-lg bg-gray-200 text-gray-700 hover:bg-gray-300 px-4 py-2 text-sm transition"
					>
						Batal
					</button>
					<button
						onClick={handleDeleteConfirm}
						className="rounded-lg bg-red-600 text-white hover:bg-red-700 px-4 py-2 text-sm font-medium transition"
					>
						Hapus
					</button>
				</div>
			</Modal>
		</div>
	);
};

export default PackageList;
