import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import api from "../../../utils/api";

 const Modal = ({ isOpen, onClose, title, children }) => {
  if (!isOpen) return null;
  return (
    <div className="fixed inset-0 bg-black bg-opacity-30 flex justify-center items-center z-50">
      <div className="bg-white rounded p-6 w-full max-w-md shadow-lg">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold">{title}</h3>
          <button
            onClick={onClose}
            className="text-gray-600 hover:text-gray-900 font-bold"
          >
            &times;
          </button>
        </div>
        {children}
      </div>
    </div>
  );
};

function CustomerManagement() {
  const { resellerId } = useParams();
  const navigate = useNavigate();

  const [reseller, setReseller] = useState(null);
  const [customers, setCustomers] = useState([]);
  const [packages, setPackages] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  // Modal states
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showResetPasswordModal, setShowResetPasswordModal] = useState(false);

  // Form states
  const [selectedCustomer, setSelectedCustomer] = useState(null);
  const [formData, setFormData] = useState({
  username: "",
  password: "",
  name: "",
  phone: "",
  package_id: null,
  payment_method: "DAILY", // Keep this
});
  const [newPassword, setNewPassword] = useState("");

 

  // Fetch reseller data
  useEffect(() => {
    const fetchReseller = async () => {
      try {
        const response = await api.get(`/users/resellers/${resellerId}`);
        setReseller(response.data);
      } catch (err) {
        setError("Failed to fetch reseller: " + err.message);
      }
    };
    fetchReseller();
  }, [resellerId]);

  // Fetch customers and packages
  const fetchCustomers = async () => {
    setIsLoading(true);
    try {
      const response = await api.get(`/users/customers?reseller_id=${resellerId}&role=CUSTOMER`);
      setCustomers(response.data);
    } catch (err) {
      setError("Failed to fetch customers: " + err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const fetchPackages = async () => {
    try {
      const response = await api.get("/packages");
      setPackages(response.data);
    } catch (err) {
      setError("Failed to fetch packages: " + err.message);
    }
  };

  useEffect(() => {
    fetchCustomers();
    fetchPackages();
  }, [resellerId]);

  // Modal handlers
  const openCreateModal = () => {
    setFormData({
      username: "",
      password: "",
      name: "",
      phone: "",
      package_id: null,
      payment_method: "DAILY",
    });
    setSelectedCustomer(null);
    setShowCreateModal(true);
  };

  const openEditModal = (customer) => {
    setSelectedCustomer(customer);
    setFormData({
      username: customer.username || "",
      password: "",
      name: customer.name || "",
      phone: customer.phone || "",
      package_id: customer.package_id || null,
      payment_method: customer.payment_method || "DAILY",
    });
    setShowEditModal(true);
  };

  const openResetPasswordModal = (customer) => {
    setSelectedCustomer(customer);
    setNewPassword("");
    setShowResetPasswordModal(true);
  };

  const closeModals = () => {
    setShowCreateModal(false);
    setShowEditModal(false);
    setShowResetPasswordModal(false);
    setSelectedCustomer(null);
    setFormData({
      username: "",
      password: "",
      name: "",
      phone: "",
      package_id: null,
      payment_method: "DAILY",
    });
    setNewPassword("");
  };

  // Form input change handler
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: name === "package_id" ? (value ? parseInt(value) : null) : value,
    }));
  };

  // Create customer
  // Replace the exports.createCustomer with this frontend version:
const createCustomer = async () => {
  if (!formData.username || !formData.password) {
    alert("Username and password are required");
    return;
  }
  
  setIsLoading(true);
  try {
    await api.post("/users/customers", {
      username: formData.username,
      password: formData.password,
      name: formData.name,
      phone: formData.phone,
      package_id: formData.package_id,
      payment_method: formData.payment_method, // Include payment method
      role: "CUSTOMER",
      reseller_id: parseInt(resellerId),
    });
    alert("Customer created successfully");
    closeModals();
    fetchCustomers();
  } catch (err) {
    alert("Failed to create customer: " + (err.response?.data?.message || err.message));
  } finally {
    setIsLoading(false);
  }
};

  // Update customer
  const updateCustomer = async () => {
    if (!selectedCustomer) return;
    setIsLoading(true);
    try {
      await api.put(`/users/customers/${selectedCustomer.id}`, {
        name: formData.name,
        phone: formData.phone,
        package_id: formData.package_id,
        payment_method: formData.payment_method,
      });
      alert("Customer updated successfully");
      closeModals();
      fetchCustomers();
    } catch (err) {
      alert("Failed to update customer: " + (err.response?.data?.message || err.message));
    } finally {
      setIsLoading(false);
    }
  };

  // Delete customer
  const deleteCustomer = async (customerId) => {
    if (!window.confirm("Are you sure you want to delete this customer?")) return;
    setIsLoading(true);
    try {
      await api.delete(`/users/customers/${customerId}`);
      alert("Customer deleted successfully");
      fetchCustomers();
    } catch (err) {
      alert("Failed to delete customer: " + (err.response?.data?.message || err.message));
    } finally {
      setIsLoading(false);
    }
  };

  // Reset password
  const resetPassword = async () => {
    if (!newPassword) {
      alert("New password is required");
      return;
    }
    if (!selectedCustomer) return;
    setIsLoading(true);
    try {
      await api.post(`/users/${selectedCustomer.id}/reset-password`, {
        newPassword,
      });
      alert("Password reset successfully");
      closeModals();
    } catch (err) {
      alert("Failed to reset password: " + (err.response?.data?.message || err.message));
    } finally {
      setIsLoading(false);
    }
  };

  const getPackageName = (packageId) => {
    const pkg = packages.find((p) => p.id === packageId);
    return pkg ? pkg.name : "No package";
  };

  return (
    <div className="p-6 bg-white rounded-xl shadow-md">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-bold">
          {reseller ? `Customers of Reseller: ${reseller.name}` : "Loading reseller..."}
        </h2>
        <button
          onClick={() => navigate(-1)}
          className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded"
        >
          Back to Resellers
        </button>
      </div>

      {error && (
        <div className="mb-4 p-2 bg-red-100 text-red-700 rounded">{error}</div>
      )}

      <div className="mb-6">
        <button
          onClick={openCreateModal}
          className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
          Create New Customer
        </button>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Name
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Phone
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Package
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Payment Method
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {customers.map((customer) => (
              <tr key={customer.id}>
                <td className="px-6 py-4 whitespace-nowrap">{customer.name}</td>
                <td className="px-6 py-4 whitespace-nowrap">{customer.phone || "-"}</td>
                <td className="px-6 py-4 whitespace-nowrap">{getPackageName(customer.package_id)}</td>
                <td className="px-6 py-4 whitespace-nowrap">{customer.payment_method || "-"}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                <div className="flex flex-col gap-1">
                  <div className="grid grid-cols-1 lg:grid-cols-4 gap-2"> 
                  <button
                    onClick={() => openEditModal(customer)}
                    className="text-yellow-600 hover:text-yellow-900 text-xs"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => openResetPasswordModal(customer)}
                    className="text-purple-600 hover:text-purple-900 text-xs"
                  >
                    Reset Password
                  </button>
                  <button
                    onClick={() => deleteCustomer(customer.id)}
                    className="text-red-600 hover:text-red-900 text-xs"
                  >
                    Delete
                  </button>
                  </div>
                </div>
              </td>

              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Create Customer Modal */}
      <Modal isOpen={showCreateModal} onClose={closeModals} title="Create Customer">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input
            type="text"
            name="username"
            placeholder="Username"
            value={formData.username}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <input
            type="text"
            name="name"
            placeholder="Name"
            value={formData.name}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <input
            type="text"
            name="phone"
            placeholder="Phone"
            value={formData.phone}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <select
            name="package_id"
            value={formData.package_id || ""}
            onChange={handleInputChange}
            className="border p-2 rounded"
          >
            <option value="">Select Package</option>
            {packages.map((pkg) => (
              <option key={pkg.id} value={pkg.id}>
                {pkg.name}
              </option>
            ))}
          </select>
          <select
            name="payment_method"
            value={formData.payment_method}
            onChange={handleInputChange}
            className="border p-2 rounded"
          >
            <option value="DAILY">Daily</option>
            <option value="WEEKLY">Weekly</option>
            <option value="MONTHLY">Monthly</option>
          </select>
        </div>
        <div className="mt-4 flex justify-end space-x-2">
          <button
            onClick={createCustomer}
            className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
            disabled={isLoading}
          >
            Create
          </button>
          <button
            onClick={closeModals}
            className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
          >
            Cancel
          </button>
        </div>
      </Modal>

      {/* Edit Customer Modal */}
      <Modal isOpen={showEditModal} onClose={closeModals} title="Edit Customer">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input
            type="text"
            name="name"
            placeholder="Name"
            value={formData.name}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <input
            type="text"
            name="phone"
            placeholder="Phone"
            value={formData.phone}
            onChange={handleInputChange}
            className="border p-2 rounded"
          />
          <select
            name="package_id"
            value={formData.package_id || ""}
            onChange={handleInputChange}
            className="border p-2 rounded"
          >
            <option value="">Select Package</option>
            {packages.map((pkg) => (
              <option key={pkg.id} value={pkg.id}>
                {pkg.name}
              </option>
            ))}
          </select>
          <select
          name="payment_method"
          value={formData.payment_method}
          onChange={handleInputChange}
          className="border p-2 rounded"
        >
          <option value="DAILY">Daily</option>
          <option value="WEEKLY">Weekly</option>
          <option value="MONTHLY">Monthly</option>
        </select>
        </div>
        <div className="mt-4 flex justify-end space-x-2">
          <button
            onClick={updateCustomer}
            className="bg-yellow-600 text-white px-4 py-2 rounded hover:bg-yellow-700"
            disabled={isLoading}
          >
            Update
          </button>
          <button
            onClick={closeModals}
            className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
          >
            Cancel
          </button>
        </div>
      </Modal>

      {/* Reset Password Modal */}
      <Modal
        isOpen={showResetPasswordModal}
        onClose={closeModals}
        title="Reset Password"
      >
        <input
          type="password"
          placeholder="New Password"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
          className="border p-2 rounded w-full mb-4"
        />
        <div className="flex justify-end space-x-2">
          <button
            onClick={resetPassword}
            className="bg-purple-600 text-white px-4 py-2 rounded hover:bg-purple-700"
            disabled={isLoading}
          >
            Reset Password
          </button>
          <button
            onClick={closeModals}
            className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
          >
            Cancel
          </button>
        </div>
      </Modal>
    </div>
  );
}

export default CustomerManagement;
