import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

const ResellerTable = ({
	resellers = [],
	customers = [],
	packages = [],
	onCreateReseller,
	onUpdateReseller,
	onDeleteReseller,
	onAddCustomer,
	onUpdateCustomer,
	onDeleteCustomer,
	onUpdateCustomerPackage,
}) => {
	const [editingResellerId, setEditingResellerId] = useState(null);
	const [editingCustomerId, setEditingCustomerId] = useState(null);
	const [newResellerName, setNewResellerName] = useState("");
	const [newCustomerName, setNewCustomerName] = useState("");
	const navigate = useNavigate();
	const [newCustomerPackageId, setNewCustomerPackageId] = useState(null);
	const [newCustomerPaymentMethod, setNewCustomerPaymentMethod] =
		useState("DAILY");

	// Handlers for reseller editing
	const startEditReseller = (reseller) => {
		setEditingResellerId(reseller.id);
		setNewResellerName(reseller.name);
	};

	const cancelEditReseller = () => {
		setEditingResellerId(null);
		setNewResellerName("");
	};

	const saveReseller = (id) => {
		onUpdateReseller(id, { name: newResellerName });
		setEditingResellerId(null);
		setNewResellerName("");
	};

	// Handlers for customer editing
	const startEditCustomer = (customer) => {
		setEditingCustomerId(customer.id);
		setNewCustomerName(customer.name);
		setNewCustomerPackageId(customer.package ? customer.package.id : null);
		setNewCustomerPaymentMethod(customer.payment_method || "DAILY");
	};

	const cancelEditCustomer = () => {
		setEditingCustomerId(null);
		setNewCustomerName("");
		setNewCustomerPackageId(null);
		setNewCustomerPaymentMethod("DAILY");
	};

	const saveCustomer = (id) => {
		onUpdateCustomer(id, { name: newCustomerName });
		onUpdateCustomerPackage(id, {
			packageId: newCustomerPackageId,
			paymentMethod: newCustomerPaymentMethod,
		});
		setEditingCustomerId(null);
		setNewCustomerName("");
		setNewCustomerPackageId(null);
		setNewCustomerPaymentMethod("DAILY");
	};

	// Handler for adding new reseller
	const handleCreateReseller = () => {
		if (newResellerName.trim() === "") return;
		onCreateReseller({ name: newResellerName });
		setNewResellerName("");
	};

	// Handler for adding new customer under reseller
	const handleAddCustomer = (resellerId) => {
		if (newCustomerName.trim() === "") return;
		onAddCustomer(resellerId, {
			name: newCustomerName,
			package_id: newCustomerPackageId,
			payment_method: newCustomerPaymentMethod,
		});
		setNewCustomerName("");
		setNewCustomerPackageId(null);
		setNewCustomerPaymentMethod("DAILY");
	};

	const handleViewCustomers = (resellerId) => {
		navigate(`/admin/resellers/${resellerId}/customers`);
	};

	return (
		<div className="overflow-x-auto">
			<h3 className="text-xl font-bold mb-4">Resellers</h3>

			<div className="mb-4 flex flex-col lg:flex-row lg:items-center gap-2">
				<input
					type="text"
					placeholder="New reseller name"
					value={newResellerName}
					onChange={(e) => setNewResellerName(e.target.value)}
					className="border border-gray-300 rounded px-3 py-2 w-full lg:w-auto"
				/>
				<button
					onClick={handleCreateReseller}
					className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded transition"
				>
					Add Reseller
				</button>
			</div>

			<table className="min-w-full divide-y divide-gray-200 bg-white shadow rounded-lg overflow-hidden">
				<thead className="bg-gray-50">
					<tr>
						<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
							Reseller
						</th>
						<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
							Customers
						</th>
						<th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
							Actions
						</th>
					</tr>
				</thead>
				<tbody className="divide-y divide-gray-100">
					{resellers.map((reseller) => (
						<tr key={reseller.id}>
							<td className="px-6 py-4 whitespace-nowrap">
								<div className="flex items-center">
									<div className="flex-shrink-0 h-10 w-10 rounded-full bg-blue-100 flex items-center justify-center">
										<span className="text-blue-600 font-medium">
											{reseller.name.charAt(0)}
										</span>
									</div>
									<div className="ml-4">
										{editingResellerId === reseller.id ? (
											<input
												type="text"
												value={newResellerName}
												onChange={(e) => setNewResellerName(e.target.value)}
												className="border p-1 rounded"
											/>
										) : (
											<div className="text-sm font-medium text-gray-900">
												{reseller.name}
											</div>
										)}
									</div>
								</div>
							</td>

							<td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
								{customers.filter((c) => c.reseller_id === reseller.id).length}
							</td>

							<td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
								<div className="grid grid-cols-1 lg:grid-cols-3 gap-2">
									{editingResellerId === reseller.id ? (
										<>
											<button
												onClick={() => saveReseller(reseller.id)}
												className="text-green-600 hover:text-green-800 text-xs"
											>
												Save
											</button>
											<button
												onClick={cancelEditReseller}
												className="text-red-600 hover:text-red-800 text-xs"
											>
												Cancel
											</button>
										</>
									) : (
										<>
											<button
												onClick={() => startEditReseller(reseller)}
												className="text-blue-600 hover:text-blue-800 text-xs"
											>
												Edit
											</button>
											<button
												onClick={() => onDeleteReseller(reseller.id)}
												className="text-red-600 hover:text-red-800 text-xs"
											>
												Delete
											</button>
											<button
												onClick={() => handleViewCustomers(reseller.id)}
												className="bg-blue-500 hover:bg-blue-700 text-white font-medium py-1 px-3 rounded text-xs transition"
											>
												View Customers
											</button>
										</>
									)}
								</div>
							</td>
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
};

export default ResellerTable;
