import React, { useState } from "react";
import api from "../../../utils/api";

const Modal = ({ isOpen, onClose, title, children }) => {
	if (!isOpen) return null;
	return (
		<div className="fixed inset-0 bg-black bg-opacity-30 flex justify-center items-center z-50">
			<div className="bg-white rounded p-6 w-full max-w-md shadow-lg">
				<div className="flex justify-between items-center mb-4">
					<h3 className="text-lg font-semibold">{title}</h3>
					<button
						onClick={onClose}
						className="text-gray-600 hover:text-gray-900 font-bold"
					>
						&times;
					</button>
				</div>
				{children}
			</div>
		</div>
	);
};

const CustomerTable = ({
	customers,
	packages,
	onSelectCustomer,
	onRefresh,
}) => {
	const [editingCustomer, setEditingCustomer] = useState(null);
	const [newCustomerData, setNewCustomerData] = useState({
		username: "",
		password: "",
		name: "",
		phone: "",
		role: "customer",
		package_id: null,
	});
	const [showCreateModal, setShowCreateModal] = useState(false);
	const [showEditModal, setShowEditModal] = useState(false);
	const [showResetPasswordModal, setShowResetPasswordModal] = useState(false);
	const [resetPasswordCustomerId, setResetPasswordCustomerId] = useState(null);
	const [newPassword, setNewPassword] = useState("");

	const handleInputChange = (e) => {
		const { name, value } = e.target;
		setNewCustomerData((prev) => ({ ...prev, [name]: value }));
	};

	const openCreateModal = () => {
		setNewCustomerData({
			username: "",
			password: "",
			name: "",
			phone: "",
			role: "customer",
			package_id: null,
		});
		setShowCreateModal(true);
	};

	const openEditModal = (customer) => {
		setEditingCustomer(customer);
		setNewCustomerData({
			username: customer.username || "",
			name: customer.name || "",
			phone: customer.phone || "",
			password: "",
			role: customer.role || "customer",
			package_id: customer.package_id || null,
		});
		setShowEditModal(true);
	};

	const closeModals = () => {
		setShowCreateModal(false);
		setShowEditModal(false);
		setShowResetPasswordModal(false);
		setEditingCustomer(null);
		setResetPasswordCustomerId(null);
		setNewPassword("");
	};

	const createCustomer = async () => {
		try {
			if (
				!newCustomerData.username ||
				!newCustomerData.name ||
				!newCustomerData.password
			) {
				alert("Username, Name, and Password are required");
				return;
			}

			const payload = {
				username: newCustomerData.username,
				password: newCustomerData.password,
				name: newCustomerData.name,
				phone: newCustomerData.phone || null, // Make optional
				role: "CUSTOMER", // Ensure uppercase if required by backend
				package_id: newCustomerData.package_id || null, // Make optional
			};

			await api.post("/customers", payload); // Try different endpoints
			// or: await api.post("/users", payload);
			// or: await api.post("/users/customers", payload);

			alert("Customer created successfully");
			closeModals();
			onRefresh();
		} catch (error) {
			console.error("Create customer error:", error.response?.data || error);
			alert(
				"Failed to create customer: " +
					(error.response?.data?.message || error.message)
			);
		}
	};

	const updateCustomer = async () => {
		try {
			if (!editingCustomer) return;
			await api.put(`/users/customers/${editingCustomer.id}`, newCustomerData);
			alert("Customer updated successfully");
			closeModals();
			onRefresh();
		} catch (error) {
			alert(
				"Failed to update customer: " +
					(error.response?.data?.message || error.message)
			);
		}
	};

	const deleteCustomer = async (customerId) => {
		if (!window.confirm("Are you sure you want to delete this customer?"))
			return;
		try {
			await api.delete(`/users/customers/${customerId}`);
			alert("Customer deleted successfully");
			onRefresh();
		} catch (error) {
			alert(
				"Failed to delete customer: " +
					(error.response?.data?.message || error.message)
			);
		}
	};

	const openResetPasswordModalFunc = (customerId) => {
		setResetPasswordCustomerId(customerId);
		setNewPassword("");
		setShowResetPasswordModal(true);
	};

	const resetPassword = async () => {
		if (!newPassword) {
			alert("New password is required");
			return;
		}
		try {
			await api.post(
				`/users/customers/${resetPasswordCustomerId}/reset-password`,
				{
					newPassword,
				}
			);
			alert("Password reset successfully");
			closeModals();
		} catch (error) {
			alert(
				"Failed to reset password: " +
					(error.response?.data?.message || error.message)
			);
		}
	};

	const customerPackageName = (customer) => {
		const pkg = packages.find((p) => p.id === customer.package_id);
		return pkg ? pkg.name : "No package";
	};

	return (
		<div>
			<button
				onClick={openCreateModal}
				className="mb-4 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
			>
				Create New Customer
			</button>

			<Modal
				isOpen={showCreateModal}
				onClose={closeModals}
				title="Create Customer"
			>
				<div className="grid grid-cols-1 md:grid-cols-2 gap-4">
					<input
						type="text"
						name="name"
						placeholder="Name"
						value={newCustomerData.name}
						onChange={handleInputChange}
						className="border p-2 rounded"
					/>
					<input
						type="text"
						name="username"
						placeholder="Username"
						value={newCustomerData.username}
						onChange={handleInputChange}
						className="border p-2 rounded"
						disabled={showEditModal}
					/>
					<input
						type="text"
						name="phone"
						placeholder="Phone"
						value={newCustomerData.phone}
						onChange={handleInputChange}
						className="border p-2 rounded"
					/>
					<input
						type="password"
						name="password"
						placeholder="Password"
						value={newCustomerData.password}
						onChange={handleInputChange}
						className="border p-2 rounded"
					/>
				</div>
				<div className="mt-4 flex justify-end space-x-2">
					<button
						onClick={createCustomer}
						className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
					>
						Create
					</button>
					<button
						onClick={closeModals}
						className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
					>
						Cancel
					</button>
				</div>
			</Modal>

			<Modal isOpen={showEditModal} onClose={closeModals} title="Edit Customer">
				<div className="grid grid-cols-1 md:grid-cols-2 gap-4">
					<input
						type="text"
						name="name"
						placeholder="Name"
						value={newCustomerData.name}
						onChange={handleInputChange}
						className="border p-2 rounded"
					/>
					<input
						type="email"
						name="email"
						placeholder="Email"
						value={newCustomerData.email}
						onChange={handleInputChange}
						className="border p-2 rounded"
						disabled
					/>
					<input
						type="text"
						name="phone"
						placeholder="Phone"
						value={newCustomerData.phone}
						onChange={handleInputChange}
						className="border p-2 rounded"
					/>
				</div>
				<div className="mt-4 flex justify-end space-x-2">
					<button
						onClick={updateCustomer}
						className="bg-yellow-600 text-white px-4 py-2 rounded hover:bg-yellow-700"
					>
						Update
					</button>
					<button
						onClick={closeModals}
						className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
					>
						Cancel
					</button>
				</div>
			</Modal>

			<Modal
				isOpen={showResetPasswordModal}
				onClose={closeModals}
				title="Reset Password"
			>
				<input
					type="password"
					placeholder="New Password"
					value={newPassword}
					onChange={(e) => setNewPassword(e.target.value)}
					className="border p-2 rounded w-full mb-4"
				/>
				<div className="flex justify-end space-x-2">
					<button
						onClick={resetPassword}
						className="bg-purple-600 text-white px-4 py-2 rounded hover:bg-purple-700"
					>
						Reset Password
					</button>
					<button
						onClick={closeModals}
						className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
					>
						Cancel
					</button>
				</div>
			</Modal>

			<div className="overflow-x-auto">
				<table className="min-w-full divide-y divide-gray-200">
					<thead className="bg-gray-50">
						<tr>
							<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
								Customer
							</th>
							<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
								Package
							</th>
							<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
								Payment Status
							</th>
							<th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
								Actions
							</th>
						</tr>
					</thead>
					<tbody className="bg-white divide-y divide-gray-200">
						{customers.map((customer) => (
							<tr key={customer.id}>
								<td className="px-6 py-4 whitespace-nowrap">
									<div className="flex items-center">
										<div className="flex-shrink-0 h-10 w-10 rounded-full bg-indigo-100 flex items-center justify-center">
											<span className="text-indigo-600 font-medium">
												{customer.name.charAt(0)}
											</span>
										</div>
										<div className="ml-4">
											<div className="text-sm font-medium text-gray-900">
												{customer.name}
											</div>
											<div className="text-sm text-gray-500">
												{customer.email}
											</div>
										</div>
									</div>
								</td>
								<td className="px-6 py-4 whitespace-nowrap">
									{customerPackageName(customer)}
								</td>
								<td className="px-6 py-4 whitespace-nowrap">
									<span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
										Active
									</span>
								</td>
								<td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
									<div className="grid grid-cols-1 lg:grid-cols-4 gap-2">
										<button
											onClick={() => onSelectCustomer(customer)}
											className="text-indigo-600 hover:text-indigo-900 text-xs"
										>
											View Payments
										</button>
										<button
											onClick={() => openEditModal(customer)}
											className="text-yellow-600 hover:text-yellow-900 text-xs"
										>
											Edit
										</button>
										<button
											onClick={() => openResetPasswordModalFunc(customer.id)}
											className="text-purple-600 hover:text-purple-900 text-xs"
										>
											Reset Password
										</button>
										<button
											onClick={() => deleteCustomer(customer.id)}
											className="text-red-600 hover:text-red-900 text-xs"
										>
											Delete
										</button>
									</div>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			</div>
		</div>
	);
};

export default CustomerTable;
